h2 {
  text-align: center;
  color: #2e2d2d;
  margin: 0 auto 18px;
}

.line-result {
  margin: 2px 0 10px;
  display: flex;
  justify-content: space-between;
  & > span:first-child {
    color: black;
    margin-right: 14px;
  }
}

.container-result {
  display: flex;
  justify-content: center;
  gap: 80px;
}

span {
  font-weight: bold;
  font-size: 14px;
  color: black;
}
